import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutusPageComponent } from './modules/aboutus/aboutus-page/aboutus-page.component';
import { BlogPageComponent } from './modules/blog/blog-page/blog-page.component';
import { ContactusPageComponent } from './modules/contactus/contactus-page/contactus-page.component';
import { FurniturePageComponent } from './modules/furniture/furniture-page/furniture-page.component';
import { HomePageComponent } from './modules/home/home-page/home-page.component';
import { NewsPageComponent } from './modules/news/news-page/news-page.component';
import { ProjectsPageComponent } from './modules/projects/projects-page/projects-page.component';
import { ServicesPageComponent } from './modules/services/services-page/services-page.component';
import { TeamPageComponent } from './modules/team/team-page/team-page.component';
import { VideosPageComponent } from './modules/videos/videos-page/videos-page.component';

const routes: Routes = [
  { path: '', component: HomePageComponent },
  { path: 'home', redirectTo: '', pathMatch: 'full' },
  { path: 'aboutus', component: AboutusPageComponent },
  { path: 'team', component: TeamPageComponent },
  { path: 'projects', component: ProjectsPageComponent },
  { path: 'services', component: ServicesPageComponent },
  { path: 'furniture', component: FurniturePageComponent },
  { path: 'videos', component: VideosPageComponent },
  { path: 'news', component: NewsPageComponent },
  { path: 'blog', component: BlogPageComponent },
  { path: 'contactus', component: ContactusPageComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
