import { AfterViewInit, Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import Swiper, { Autoplay } from 'swiper';

@Component({
  selector: 'app-contactus-page',
  templateUrl: './contactus-page.component.html',
  styleUrls: ['./contactus-page.component.css']
})
export class ContactusPageComponent implements OnInit, AfterViewInit {

  constructor() { }

  ngOnInit(): void {
  }

  ngAfterViewInit() {
    this.initSwiperContainer();
  }

  initSwiperContainer() {
    let s1 = new Swiper('.contactus-swiper', {
      speed: 1000,
      direction: 'vertical'
    });

    let winWidth: number = window.innerWidth;
    let winHeight: number = window.innerHeight;

    $("div.swiper-slide").width(winWidth).height(winHeight);
  }

}
